html {
  font-size: 16px;
  font-family: Arial;
}

body {
  margin: 0;
}

.nav {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  color: white;
  text-decoration: none;
  padding: 10px 20px;
  display: inline-block;
}